import React from 'react';
import Costumers from '../images/costumers.png'
import Item1 from '../images/item-1.png'
import Item2 from '../images/item-2.png'
import Item3 from '../images/item-3.png'

function Home() {
    function burger(e) { 
        e.preventDefault();
        const burg = document.getElementById("menu");
        const close = document.getElementById("burger");
        if (burg.style.display === "none") {
            burg.style.display = "block";
            close.classList.add("is-clicked");
        }

        else {
            burg.style.display = "none";
            close.classList.remove("is-clicked");

        }


    }
    return (
        <div>
            <header>
                <div className="row">
                    <form action="index.html">
                        <div className="logo">
                            <a href="/" className="logo-icon">Math Modeling</a>
                            <a href="/" className="logo-icon-2"> </a>
                        </div>
                    </form>
                    <nav id="main-nav-wrap">
                        <ul className="main-navigation" id="menu">
                            <li><a className="smoothscroll" href="/about" title="">About Us</a></li>
                            <li><a className="smoothscroll" href="#process" title="">Costumers</a></li>
                            <li><a className="smoothscroll" href="#capability" title="">Capability</a></li>
                            <li><a className="smoothscroll" href="#testimonials" title="">Contact Us</a></li>
                            <li><a className="smoothscroll" href="/" title="">Case Studies</a></li>
                            <li><a className="smoothscroll butt" href='/demo' title="">Try Demo</a></li>
                        </ul>
                    </nav>
                    <a className="menu-toggle" id="burger" onClick={burger} href="#"><span>Menu</span></a>
                </div>

            </header>
            <section id="intro">

                <div className="shadow-overlay"></div>

                <div className="intro-content">
                    <div className="row">

                        <div className="col-twelve">
                            <h1>Math Modeling</h1>
                            <div className='desc'>
                                <h5>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Pharetra, enim quis <br /> et suspendisse
                                    mi id. Nibh enim eget vitae risus malesuada sapien. Amet <br /> faucibus tortor quisque vivamus
                                    vulputate. Consectetur volutpat vulputate<br /> sit cras massa malesuada sed risus amet. `</h5>
                            </div>
                            <a href="/" className="butt">Read More</a>
                        </div>

                    </div>
                </div>

            </section>
            <section id="process">

                <div className="section-intro">
                    <div className="col-twelve">
                        <h1>Costumers</h1>
                    </div>
                    <div className="process-content">
                        <img src={Costumers} className="center-side" alt="costumers" />
                    </div>
                </div>
            </section>
            <section id="capability">

                <div className="section-intro">
                    <div className="col-twelve">
                        <h1>Capability</h1>
                    </div>
                </div>
                <div className="capability-content">
                    <div className="center-side">
                        <div className="item">
                            <img src={Item3} alt="item3" />
                            <h1>Mathematical Optimization</h1>
                        </div>
                        <div className="item">
                            <img src={Item2} alt="item2" />
                            <h1>Machine Learning</h1>
                        </div>
                        <div className="item">
                            <img src={Item1} alt="item1" />
                            <h1>Stimulation</h1>
                        </div>
                    </div>
                </div>
                <div className="bottom-side">
                    <a href="/" className="butt">More</a>
                </div>
            </section>
            <section id="testimonials">
                <div className="flex-container">
                    <div className="col-twelve">
                        <h1>Contact Us</h1>
                    </div>
                </div>

                <div id="testimonial-slider" className="flexslider">
                    <li>
                        <div className="testimonial-author">
                            <form action="#">
                                <div className="author-info">
                                    <input type="text" className="input" />
                                </div>
                                <div className="author-info">
                                    <input type="text" className="input" />
                                </div>
                                <div className="author-info">
                                    <textarea type="text" className="input-ds"></textarea>
                                </div>
                                <input type="submit" className="butt" value="Send" />
                            </form>
                        </div>
                    </li>
                </div>

            </section>
            <footer className="footer-bottom">
                <div>
                    <div className="footer-main">
                        <a href="/home" className="footer-logo"> </a>
                        <a href="/home" className="footer-logo-2"> </a>
                    </div>
                    <div className="footer-nav">
                        <nav>
                            <ul>
                                <li id="first"><a className="smoothscroll" href="/about" title="">About Us</a></li>
                                <li><a className="smoothscroll" href="/home/#process" title="">Costumers</a></li>
                                <li><a className="smoothscroll" href="/home/#capability" title="">Capability</a></li>
                                <li><a className="smoothscroll" href="/home/#testimonials" title="">Contact Us</a></li>
                            </ul>
                        </nav>
                    </div>
                    <div className="phone"><a href="tel:+374 54 78 52">Tel: +374 54 78 52</a></div>
                    <div className='location'><a href="https://bit.ly/3ca8Rhf" target="_blank" rel="noopener noreferrer">Location : St. Armenakyan,Yerevan</a></div>
                </div>
            </footer>
        </div>
    );

}

export default Home;