import React, { useState } from 'react';

function Demo() {
  const [openModal, setOpenModal] = useState();
  function protonation(evt) {
    const element = evt.target;
    if (element.value != 0) {
      if (element.value < 0.03 && element.value !== "" && element.value !== 0) {
        element.value = "";
        element.placeholder = "Minimum: 0.03g";
        element.style.border = "2px solid red";
        element.style.transition = "0.5s";
        setTimeout(() => {
          element.style.transition = "0.5s";
          element.style.border = "2px solid #9A9A9A";
          element.placeholder = "The amout of benzoic acid (g)";
        }, 1500);
      }
      if (element.value > 1.5 && element.value !== "") {
        element.value = "";
        element.placeholder = "Maximum: 1.5g";
        element.style.border = "2px solid red";
        element.style.transition = "0.5s";
        setTimeout(() => {
          element.style.transition = "0.5s";
          element.style.border = "2px solid #9A9A9A";
          element.placeholder = "The amout of benzoic acid (g)";
        }, 1500);
      }
    }
  }
  function elem(evt) {

    const element = document.getElementById('element');
    const b = evt.target;
    let event = b.value;
    element.innerHTML = event;
  }
  function volatility(evt) {
    const element = evt.target;
    if (element.value < 290.15 && element.value !== "") {
      element.value = "";
      element.placeholder = "Minimum: 290.15";
      element.style.border = "2px solid red";
      element.style.transition = "0.5s";
      setTimeout(() => {
        element.style.transition = "0.5s";
        element.style.border = "2px solid #9A9A9A";
        element.placeholder = "the temperature in Kelvin";
      }, 1500);

      if (element.value > 630.15 && element.value !== "") {
        element.value = "";
        element.placeholder = "Maximum:  630.15";
        element.style.border = "2px solid red";
        element.style.transition = "0.5s";
        setTimeout(() => {
          element.style.transition = "0.5s";
          element.style.border = "2px solid #9A9A9A";
          element.placeholder = "the temperature in Kelvin";
        }, 1500);
      }
    }
  }
  function solubility(evt) {
    const element = evt.target;
    if (element.value != 0) {
      if (element.value < 0 && element.value !== "") {
        element.value = "";
        element.placeholder = "Minimum: 0g";
        element.style.border = "2px solid red";
        element.style.transition = "0.5s";
        setTimeout(() => {
          element.style.transition = "0.5s";
          element.style.border = "2px solid #9A9A9A";
          element.placeholder = "the mass fraction of ibuprofen";
        }, 1500);
      }
      if (element.value > 1 && element.value !== "") {
        element.value = "";
        element.placeholder = "Maximum: 1g";
        element.style.border = "2px solid red";
        element.style.transition = "0.5s";
        setTimeout(() => {
          element.style.transition = "0.5s";
          element.style.border = "2px solid #9A9A9A";
          element.placeholder = "the mass fraction of ibuprofen";
        }, 1500);
      }
    }
  }
  return (
    <div>
      <div className="demo">
        <div className="demo-part">Demo</div>
        <div className="logo-part">
          <a href="/" className="logo">Math Modeling</a>
          <a href="/" className="logo-icon-2">Math Modeling</a>
        </div>
        <div className="demo-math">
          <div className="demo-math-input">
            <button onClick={() => setOpenModal(openModal === 1 ? 0 : 1)} className="butt">The protonation percentage of nicotine in the water/benzoic acid
              mixture</button>
            {openModal === 1 &&
              <div id="popup" className="popup">
                <div className="popup_body">
                  <div className="popup_content">
                    <div className='input-part'>
                      <h5>input: </h5>
                      <input type="number" onChange={protonation} className="input" placeholder="The amout of benzoic acid (g)" />
                    </div>
                    <div>
                      <input type="number" onChange={protonation} className="input" placeholder="The amout of benzoic acid (g)" />
                    </div>
                    <div>
                      <input type="number" onChange={protonation} className="input" placeholder="The amout of benzoic acid (g)" />
                    </div>
                    <div>
                      <input type="text" onChange={elem} className="input" placeholder="Type Your element" />
                    </div>
                    <div className="popup-border"></div>
                    <div className="popup-txt">output: <strong id="element"></strong></div>
                  </div>
                </div>
              </div>
            }

            <div className="demo-math-input">
              <button onClick={() => setOpenModal(openModal === 2 ? 0 : 2)} className="butt">The solubility of ibuprofine in the water/propylene glycol mixture</button>
              {openModal === 2 &&
                <div id="popup-2" className="popup">
                  <div className="popup_body">
                    <div className="popup_content">
                      <div className='input-part'>
                        <h5>input: </h5>
                        <input type="number" onChange={solubility} className="input" placeholder="the mass fraction of ibuprofen" />
                      </div>
                      <div>
                        <input type="number" onChange={solubility} className="input" placeholder="the mass fraction of ibuprofen" />
                      </div>
                      <div>
                        <input type="number" onChange={solubility} className="input" placeholder="the mass fraction of ibuprofen" />
                      </div>
                      <div>
                        <input type="text" onChange={elem} className="input" placeholder="Type Your element" />
                      </div>
                      <div className="popup-border"></div>
                      <div className="popup-txt">output: <strong id="element"></strong></div>
                    </div>
                  </div>
                </div>
              }
            </div>
            <div className="demo-math-input">
              <button onClick={() => setOpenModal(openModal === 3 ? 0 : 3)} className="butt"> The volatility of ibuprofine in water</button>
              {openModal === 3 && <div id="popup-3" className="popup">
                <div className="popup_body">
                  <div className="popup_content">
                    <div className='input-part'>
                      <h5>input: </h5>
                      <input type="number" onChange={volatility} min="290.15" max="630.15" className="input" placeholder="the temperature in Kelvin" />
                    </div>
                    <div>
                      <input type="number" onChange={volatility} className="input" placeholder="the temperature in Kelvin" />
                    </div>
                    <div>
                      <input type="number" onChange={volatility} className="input" placeholder="the temperature in Kelvin" />
                    </div>
                    <div>
                      <input type="text" onChange={elem} className="input" placeholder="Type Your element" />
                    </div>
                    <div className="popup-border"></div>
                    <div className="popup-txt">output: <strong id="element"></strong></div>
                  </div>
                </div>
              </div>}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Demo;